  font-style: normal;
}
@font-face {
  font-family: "Metropolis";
  src: url("../fonts/Metropolis-ExtraBold.otf");
  font-weight: 800;
  font-style: normal;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

